<template>
  <div class="page">
    <div class="bg">
      <canvas id="myCanvas"></canvas>
      <!--      <img :src="bginfo.pic" v-if="bginfo.pic">-->
      <!--      <video :src="bginfo.video" v-else preload="auto" autoplay loop muted @click.stop="openvo"></video>-->
      <!--      <div class="text">-->
      <!--        <span>{{bginfo.picname}}</span>-->
      <!--        <span>{{bginfo.picadd}}</span>-->
      <!--      </div>-->
    </div>
    <div class="content" style="flex-direction: column">
      <div class="top">
        <router-link tag="a" :to="'/articles/'+articles.id">
          <div class="time">{{articles.time}}</div>
          <div class="title">{{articles.title}}</div>
          <div class="img"><img :src="articles.tpic"></div>
        </router-link>
      </div>
      <div class="top">
        <router-link tag="a" :to="'/lifes/'+lifes.id">
          <div class="time">{{lifes.time}}</div>
          <div class="title">{{lifes.title}}</div>
          <div class="img"><img :src="lifes.tpic"></div>
        </router-link>
      </div>
    </div>
  </div>
</template>
<script>
import {list} from '@/api/api'
import * as THREE from 'three';
import {OrbitControls} from 'three/examples/jsm/controls/OrbitControls.js';
import {GLTFLoader} from 'three/examples/jsm/loaders/GLTFLoader.js';


export default {
    name: 'index',
    data() {
        return {
            sw: window.innerWidth,
            sh: window.innerHeight,
            bginfo: {},
            articles: {},
            lifes: {},
        }
    },
    mounted() {
        //
        let scene = new THREE.Scene();
        scene.background = new THREE.Color(0x46bd87);
        let camera = new THREE.PerspectiveCamera(50, window.innerWidth / window.innerHeight, 0.01, 1000);
        camera.position.set(0, .1, .1);
        //
        let renderer = new THREE.WebGLRenderer({antialias: true, canvas: document.querySelector('#myCanvas')});
        renderer.setSize(window.innerWidth, window.innerHeight);
        //
        let controls = new OrbitControls(camera, renderer.domElement);
        //controls.auto
        controls.autoRotate = true
        controls.autoRotateSpeed = 2
        controls.RotateSpeed = 1
        controls.maxDistance = 2
        controls.minDistance = 2
        controls.maxPolarAngle = Math.PI / 2
        //
        const ambientLight = new THREE.AmbientLight(0xffffff,1);
        scene.add(ambientLight);
        const dirLight = new THREE.DirectionalLight(0xffffff, 4);
        dirLight.position.set(0, 10, 0);
        scene.add(dirLight);


        //GITF
        let clockP1 = new THREE.Clock(),clockP2 = new THREE.Clock()
        let mixerP1,mixerP2
        let loader = new GLTFLoader();
        loader.load(['sc.glb'], function (gltf) {
            scene.add(gltf.scene);
        });
        let loaderP1 = new GLTFLoader();
        loaderP1.load(['plane1.glb'], function (gltf) {
            scene.add(gltf.scene);
            gltf.scene.scale.set(.01,.01,.01)
            gltf.scene.position.set(0,.6,.6)
            mixerP1 = new THREE.AnimationMixer(gltf.scene);
            mixerP1.clipAction(gltf.animations[0]).play();
        });
        let loaderP2 = new GLTFLoader();
        loaderP2.load(['plane2.glb'], function (gltf) {
            scene.add(gltf.scene);
            gltf.scene.scale.set(.01,.01,.01)
            gltf.scene.position.set(0,.3,-.6)
            mixerP2 = new THREE.AnimationMixer(gltf.scene);
            mixerP2.clipAction(gltf.animations[0]).play();
        });



        //
        function animate() {
            if (mixerP1&&mixerP2) {
                mixerP1.update(clockP1.getDelta());
                mixerP2.update(clockP2.getDelta());
            }
            controls.update();
            renderer.render(scene, camera);
            requestAnimationFrame(animate);
        }

        window.addEventListener('resize', () => {
            camera.aspect = window.innerWidth / window.innerHeight;
            camera.updateProjectionMatrix();
            renderer.setSize(window.innerWidth, window.innerHeight);

        });
        animate()

    },
    created() {
        let data1 = {
            app_key: '1D5AF16FD7D90EE3C5CF70D088249505',
            model_name: 'bgimg',
            perpage: 1000
        }
        let data2 = {
            app_key: '1D5AF16FD7D90EE3C5CF70D088249505',
            model_name: 'article',
            perpage: 1000,
            order: ["mtime DESC", "id DESC"]
        }
        let data3 = {
            app_key: '1D5AF16FD7D90EE3C5CF70D088249505',
            model_name: 'life',
            perpage: 1000,
            order: ["mtime DESC", "id DESC"]
        }


        list(data1).then((data) => {
            data = data.data.list
            let xh = Math.floor(Math.random() * data.length)
            this.bginfo = data[xh]
        })
        list(data2).then((data) => {
            data = data.data.list
            this.articles = data[0]
        })
        list(data3).then((data) => {
            data = data.data.list
            this.lifes = data[0]
        })
    },
    methods: {
        openvo() {
            let target = event.currentTarget
            target.muted = false
        }
    },
    computed: {}
}
</script>

